import React, { useEffect, useState } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import MenuItem from '@mui/material/MenuItem';
import Autocomplete from '@mui/material/Autocomplete';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import Tooltip from "@mui/material/Tooltip";

import EastTwoTone from '@mui/icons-material/EastTwoTone';
import SouthTwoTone from '@mui/icons-material/SouthTwoTone';
import WestTwoTone from '@mui/icons-material/WestTwoTone';
import NorthTwoTone from '@mui/icons-material/NorthTwoTone';
import LinearScaleTwoTone from '@mui/icons-material/LinearScaleTwoTone';
import WrapTextTwoTone from '@mui/icons-material/WrapTextTwoTone';
import SortTwoTone from '@mui/icons-material/SortTwoTone';
import Grid4x4TwoTone from '@mui/icons-material/Grid4x4TwoTone';

import AlignHorizontalCenter from '@mui/icons-material/AlignHorizontalCenter';
import FileDownloadTwoTone from '@mui/icons-material/FileDownloadTwoTone';
import FileUploadTwoTone from '@mui/icons-material/FileUploadTwoTone';
import CreditCardTwoTone from '@mui/icons-material/CreditCardTwoTone';
import ViewDayTwoTone from '@mui/icons-material/ViewDayTwoTone';
import Height from '@mui/icons-material/Height';
import FormatAlignLeft from '@mui/icons-material/FormatAlignLeft';
import FormatAlignRight from '@mui/icons-material/FormatAlignRight';
import ViewArrayTwoTone from '@mui/icons-material/ViewArrayTwoTone';
import WidthFullTwoTone from '@mui/icons-material/WidthFullTwoTone';
import PauseTwoTone from '@mui/icons-material/PauseTwoTone';
import AlignVerticalCenter from '@mui/icons-material/AlignVerticalCenter';
import AlignVerticalTop from '@mui/icons-material/AlignVerticalTop';
import AlignVerticalBottom from '@mui/icons-material/AlignVerticalBottom';
import Foundation from '@mui/icons-material/Foundation';

import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepButton from '@mui/material/StepButton';

import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [];

const StyleContainer = (props, ref) => {

    const [styles, setStyles] = useState(props.defaultstyles || {});
    const [layout, setLayout] = useState(styles.display || "flex");
    const [direction, setDirection] = useState(styles.flexDirection || "row");
    const [flexwrap, setFlexwrap] = useState(styles.flexWrap || "wrap");
    const [alignContent, setAlignContent] = useState(styles.alignContent || "center");
    const [justifyContent, setJustifyContent] = useState(styles.justifyContent || "center");
    const [alignItems, setAlignItems] = useState(styles.alignItems || "center");
    const [justifyItems, setJustifyItems] = useState(styles.justifyItems || "center");
    const [margin, setMargin] = useState(styles.margin || 0);
    const [marginTop, setMarginTop] = useState(styles.marginTop || 0);
    const [marginRight, setMarginRight] = useState(styles.marginRight || 0);
    const [marginBottom, setMarginBottom] = useState(styles.marginBottom || 0);
    const [marginLeft, setMarginLeft] = useState(styles.marginLeft || 0);
    const [padding, setPadding] = useState(styles.padding || 0);
    const [paddingTop, setPaddingTop] = useState(styles.paddingTop || 0);
    const [paddingRight, setPaddingRight] = useState(styles.paddingRight || 0);
    const [paddingBottom, setPaddingBottom] = useState(styles.paddingBottom || 0);
    const [paddingLeft, setPaddingLeft] = useState(styles.paddingLeft || 0);
    const [borderTop, setBorderTop] = useState(styles.borderTop || 0);
    const [borderRight, setBorderRight] = useState(styles.borderRight || 0);
    const [borderBottom, setBorderBottom] = useState(styles.borderBottom || 0);
    const [borderLeft, setBorderLeft] = useState(styles.borderLeft || 0);
    const [border, setBorder] = useState(styles.border || 0);
    const [borderColor, setBorderColor] = useState(styles.borderColor || "black");
    const [borderStyle, setBorderStyle] = useState(styles.borderStyle || "solid");
    const [background, setBackground] = useState(styles.background || "");
    const [boxshadow, setBoxshadow] = useState(styles.boxshadow || "");
    const [columns, setColumns] = useState(styles.columns || 1);
    const [rows, setRows] = useState(styles.rows || 1);
    const [gap, setGap] = useState(styles.gap || "");
    const [height, setHeight] = useState(styles.height || "");
    const [width, setWidth] = useState(styles.width || "");
    const [minHeight, setMinHeight] = useState(styles.minHeight || "");
    const [minWidth, setMinWidth] = useState(styles.minWidth || "");
    const [maxHeight, setMaxHeight] = useState(styles.maxHeight || "");
    const [maxWidth, setMaxWidth] = useState(styles.maxWidth || "");
    const [activeStep, setActiveStep] = React.useState(0);

    const [customized, setCustomized] = useState([]);

    let defaultRenderInput = (params) => <TextField {...params} label={props.label} inputProps={{...params.inputProps, autoComplete: 'new-password'}}/>

    const getValue = function(inputValue) {
        let isNumeric = !isNaN(parseFloat(inputValue)) && isFinite(inputValue);
        return isNumeric? inputValue + "px": inputValue;
    }

    useEffect(() => {
        let updatedStyles = {...styles};
        if(customized.includes("display")) updatedStyles.display = layout;
        if(customized.includes("flexDirection")) updatedStyles.flexDirection = direction;
        if(customized.includes("flexWrap")) updatedStyles.flexWrap = flexwrap;
        if(customized.includes("margin")) updatedStyles.margin = getValue(margin);
        if(customized.includes("marginTop")) updatedStyles.marginTop = getValue(marginTop);
        if(customized.includes("marginBottom")) updatedStyles.marginBottom = getValue(marginBottom);
        if(customized.includes("marginLeft")) updatedStyles.marginLeft = getValue(marginLeft);
        if(customized.includes("marginRight")) updatedStyles.marginRight = getValue(marginRight);
        if(customized.includes("padding")) updatedStyles.padding = getValue(padding);
        if(customized.includes("paddingBottom")) updatedStyles.paddingBottom = getValue(paddingBottom);
        if(customized.includes("paddingTop")) updatedStyles.paddingTop = getValue(paddingTop);
        if(customized.includes("paddingLeft")) updatedStyles.paddingLeft = getValue(paddingLeft);
        if(customized.includes("paddingRight")) updatedStyles.paddingRight = getValue(paddingRight);
        if(customized.includes("border")) updatedStyles.border = getValue(border);
        if(customized.includes("borderTop")) updatedStyles.borderTop = getValue(borderTop);
        if(customized.includes("borderBottom")) updatedStyles.borderBottom = getValue(borderBottom);
        if(customized.includes("borderLeft")) updatedStyles.borderLeft = getValue(borderLeft);

        if(customized.includes("borderRight")) updatedStyles.borderRight = getValue(borderRight);
        if(customized.includes("borderColor")) updatedStyles.borderColor = borderColor;
        if(customized.includes("borderStyle")) updatedStyles.borderStyle = borderStyle;
        if(customized.includes("background")) updatedStyles.background = background;
        if(customized.includes("boxshadow")) updatedStyles.boxshadow = boxshadow;
        if(customized.includes("alignContent")) updatedStyles.alignContent = alignContent;
        if(customized.includes("justifyContent")) updatedStyles.justifyContent = justifyContent;
        if(customized.includes("alignItems")) updatedStyles.alignItems = alignItems;
        if(customized.includes("justifyItems")) updatedStyles.justifyItems = justifyItems;
        if(customized.includes("borderBottom")) updatedStyles.borderBottom = borderBottom;
        if(customized.includes("borderLeft")) updatedStyles.borderLeft = getValue(borderLeft);
        if(customized.includes("height")) updatedStyles.height = getValue(height);
        if(customized.includes("width")) updatedStyles.width = getValue(width);
        if(customized.includes("minHeight")) updatedStyles.minHeight = getValue(minHeight);
        if(customized.includes("minWidth")) updatedStyles.minWidth = getValue(minWidth);
        if(customized.includes("maxHeight")) updatedStyles.maxHeight = getValue(maxHeight);
        if(customized.includes("maxWidth")) updatedStyles.maxWidth = getValue(maxWidth);
    
        props.dispatchEvent(EventAPI.STYLES_UPDATED, { payload: {id: props.id || props.elementid, styles: updatedStyles}});
    }, [layout, direction, flexwrap,  margin, marginTop, marginBottom, marginLeft, marginRight, 
        padding, paddingBottom, paddingTop, paddingLeft, paddingRight, border, borderTop, borderBottom, borderLeft, borderRight, borderColor, borderStyle, 
        background, boxshadow, alignContent, justifyContent, alignItems, justifyItems, rows, columns, gap, height, width, minHeight, minWidth, maxHeight, maxWidth])

    return (
        <Box id="main" sx={{ padding: "8px 0px", width: "100%", position: "relative", display: "flex", flexDirection: "row", flexWrap: "wrap" }}>
            <Box id="stepper" sx={{ padding: "8px 0px", width: "100%", position: "relative", display: "flex", flexDirection: "row", flexWrap: "wrap" }}>
                <Stepper nonLinear activeStep={activeStep}>
                    <Step>
                        <StepButton color="inherit" onClick={() => { setActiveStep(0)}}>Spacing</StepButton>
                    </Step>
                    <Step>
                        <StepButton color="inherit" onClick={() => { setActiveStep(1)}}>Layouting</StepButton>
                    </Step>
                    <Step>
                        <StepButton color="inherit" onClick={() => { setActiveStep(2)}}>Styling</StepButton>
                    </Step>
                </Stepper>
            </Box>
            {
                activeStep == 0? (<Box id="box_border" sx={{ padding: "32px 44px", height: "200px", width: "300px", borderRadius: "2px", position: "relative", border: "1px dashed RGB(66, 80, 81)", backgroundColor: "var(--cds-border-interactive)" }}>
                    <Box id="box_border_label" sx={{ position: "absolute", top: "0px", left: "0px" }}>
                        <Box id="box_border_body" sx={{ position: "relative", display: "flex", flexDirection: "row", transform: "scale(0.8)" }}>
                            <Typography sx={{ color: "var(--cds-background-inverse)" }}>Border</Typography>
                            <input value={border} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("border"); setCustomized(customized); setBorder(event.target.value) }} style={{ marginLeft: "3px", width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-text-primary)", color: "var(--cds-background)" }}></input>
                            <Autocomplete labelId="box_border_style" id="box_border_style" renderInput={defaultRenderInput}
                                value={borderStyle} onChange={(event, selectedOption) => { event.stopPropagation(); event.preventDefault(); customized.push("borderStyle"); setCustomized(customized); setBorderStyle(selectedOption?.value || "") }}
                                sx={{ height: "20px", width: "70px", fontSize: "xx-small" }} variant="standard"
                                options={[{label: "Solid", value: "solid"}, {label: "Dashed", value: "dashed"}, {label: "Dotted", value: "dotted"}, {label: "Double", value: "double"}]}>
                            </Autocomplete>
                            <input type="color" value={borderColor} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("borderColor"); setCustomized(customized); setBorderColor(event.target.value) }} style={{ width: "30px", textAlign: "center", border: "none", margin: "0px", padding: "0px", height: "20px" }}></input>
                        </Box>
                    </Box>
                    <Box id="box_border_top" sx={{ position: "absolute", top: "6px", left: "50%", transform: "translate(-50%)" }}>
                        <input value={borderTop} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("borderTop"); setCustomized(customized); setBorderTop(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-border-interactive)", color: "white" }}></input>
                    </Box>
                    <Box id="box_border_right" sx={{ position: "absolute", top: "50%", right: "4px", transform: "translateY(-50%)" }}>
                        <input value={borderRight} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("borderRight"); setCustomized(customized);  setBorderRight(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-border-interactive)", color: "white" }}></input>
                    </Box>
                    <Box id="box_border_bottom" sx={{ position: "absolute", bottom: "6px", left: "50%", transform: "translate(-50%)" }}>
                        <input value={borderBottom} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("borderBottom"); setCustomized(customized);  setBorderBottom(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-border-interactive)", color: "white" }}></input>
                    </Box>
                    <Box id="box_border_left" sx={{ position: "absolute", top: "50%", left: "4px", transform: "translateY(-50%)" }}>
                        <input value={borderLeft} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("borderLeft"); setCustomized(customized);  setBorderLeft(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-border-interactive)", color: "white" }}></input>
                    </Box>
                    <Box id="box_border_border" sx={{ padding: "8px", display: "flex", flexGrow: 1, position: "relative", border: "1.5px solid RGB(66, 80, 81)", backgroundColor: "RGB(99, 114, 116)", borderRadius: "2px" }}>
                        <Box id="box_margin" sx={{ padding: "32px 44px", width: "100%", height: "120px", borderRadius: "2px", position: "relative", border: "1px dashed RGB(66, 80, 81)", backgroundColor: "var(--cds-link-primary)" }}>
                            <Box id="box_margin_label" sx={{ position: "absolute", top: "0px", left: "0px" }}>
                                <Typography sx={{ color: "var(--cds-background)" }}>Margin</Typography>
                            </Box>
                            <Box id="box_margin_top" sx={{ position: "absolute", top: "6px", left: "50%", transform: "translate(-50%)" }}>
                                <input value={marginTop} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("marginTop"); setCustomized(customized);  setMarginTop(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-link-primary)", color: "white" }}></input>
                            </Box>
                            <Box id="box_margin_right" sx={{ position: "absolute", top: "50%", right: "4px", transform: "translateY(-50%)" }}>
                                <input value={marginRight} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("marginRight"); setCustomized(customized);  setMarginRight(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-link-primary)", color: "white" }}></input>
                            </Box>
                            <Box id="box_margin_bottom" sx={{ position: "absolute", bottom: "6px", left: "50%", transform: "translate(-50%)" }}>
                                <input value={marginBottom} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("marginBottom"); setCustomized(customized);  setMarginBottom(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-link-primary)", color: "white" }}></input>
                            </Box>
                            <Box id="box_margin_left" sx={{ position: "absolute", top: "50%", left: "4px", transform: "translateY(-50%)" }}>
                                <input value={marginLeft} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("marginLeft"); setCustomized(customized);  setMarginLeft(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-link-primary)", color: "white" }}></input>
                            </Box>
                            <Box id="box_margin_border" sx={{ padding: "8px", display: "flex", flexGrow: 1, position: "relative", border: "1.5px solid RGB(66, 80, 81)", backgroundColor: "RGB(99, 114, 116)", borderRadius: "2px" }}>
                                <Box id="box_padding" sx={{ padding: "24px 12px", display: "flex", flexGrow: 1, position: "relative", height: "50px", border: "1.5px solid RGB(66, 80, 81)", backgroundColor: "RGB(246, 247, 247)", borderRadius: "2px" }}>
                                    <Box id="box_padding_label" sx={{ position: "absolute", top: "0px", left: "0px" }}>
                                        <Typography sx={{ color: "var(--cds-background)" }}>Padding</Typography>
                                    </Box>
                                    <Box id="box_padding_top" sx={{ position: "absolute", top: "2px", left: "50%", transform: "translate(-50%)" }}>
                                        <input value={paddingTop} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("paddingTop"); setCustomized(customized);  setPaddingTop(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-text-primary)", color: "var(--cds-background)" }}></input>
                                    </Box>
                                    <Box id="box_padding_right" sx={{ position: "absolute", top: "50%", right: "4px", transform: "translateY(-50%)" }}>
                                        <input value={paddingRight} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("paddingRight"); setCustomized(customized);  setPaddingRight(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-text-primary)", color: "var(--cds-background)" }}></input>
                                    </Box>
                                    <Box id="box_padding_bottom" sx={{ position: "absolute", bottom: "6px", left: "50%", transform: "translate(-50%)" }}>
                                        <input value={paddingBottom} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("paddingBottom"); setCustomized(customized);  setPaddingBottom(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-text-primary)", color: "var(--cds-background)" }}></input>
                                    </Box>
                                    <Box id="box_padding_left" sx={{ position: "absolute", top: "50%", left: "4px", transform: "translateY(-50%)" }}>
                                        <input value={paddingLeft} onChange={(event) => { event.stopPropagation(); event.preventDefault(); customized.push("paddingLeft"); setCustomized(customized);  setPaddingLeft(event.target.value) }} style={{ width: "40px", textAlign: "center", border: "none", height: "10px", width: "30px", padding: "4px", background: "var(--cds-text-primary)", color: "var(--cds-background)" }}></input>
                                    </Box>
                                    <Box id="box_element" sx={{ backgroundColor: "RGB(172, 180, 181)", border: "1.5px solid RGB(66,80, 81)", borderRadius: "2px", height: "90%", margin: "auto", minHeight: "20px", width: "53%" }}>
                                    </Box>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                </Box>): null
            }
            {
                activeStep == 1? (
                    <Box sx={{ marginLeft: "5px", flexWrap: "wrap", display: "flex", flexDirection: "row", width: "100%" }}>
                        <Box sx={{display: "flex", flexDirection: "column"}}>
                            <Typography sx={{color: "var(--cds-text-secondary)"}}>Layout</Typography>
                            <ToggleButtonGroup exclusive value={layout} onChange={(evt, layoutValue) => { if(!!layoutValue) { customized.push("layout"); setCustomized(customized); setLayout(layoutValue); } }} aria-label="layout=selection">
                                <ToggleButton value="flex" aria-label="flex">
                                    <Tooltip title="Flex">
                                        <SortTwoTone />
                                    </Tooltip>
                                </ToggleButton>
                                <ToggleButton value="grid" aria-label="grid">
                                    <Tooltip title="Grid">
                                        <Grid4x4TwoTone />
                                    </Tooltip>
                                </ToggleButton>
                            </ToggleButtonGroup>
                        </Box>
                        {
                            layout == "grid"? (<Box id="box_grid" sx={{ marginLeft: "5px", marginTop: "20px", width: "400px", display: "flex", flexDirection: "row" }}>
                                <TextField label="Columns" type="number" value={columns} onChange={(event) => { customized.push("columns"); setCustomized(customized); setColumns(event.target.value || 1) }} ></TextField>
                                <TextField label="Rows" type="number" value={rows} onChange={(event) => { customized.push("rows"); setCustomized(customized); setRows(event.target.value || 1) }} ></TextField>
                                <TextField label="Gap" type="string" value={gap} onChange={(event) => { customized.push("gap"); setCustomized(customized); setGap(event.target.value || "0px") }} ></TextField>
                            </Box>): (
                                <Box sx={{marginLeft: "5px", display: "flex", flexDirection: "column"}}>
                                    <Typography sx={{color: "var(--cds-text-secondary)"}}>Wrap</Typography>
                                    <Box id="box_flex_wrap" sx={{ display: "flex", flexDirection: "row" }}>
                                        <ToggleButtonGroup exclusive value={flexwrap} onChange={(evt, wrapValue) => { if(!!wrapValue) { customized.push("flexwrap"); setCustomized(customized); setFlexwrap(wrapValue); } }} aria-label="layout=selection">
                                            <ToggleButton value="nowrap" aria-label="no wrap">
                                                <Tooltip title="No Wrap">
                                                    <LinearScaleTwoTone />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value="wrap" aria-label="wrap">
                                                <Tooltip title="Wrap">
                                                    <WrapTextTwoTone />
                                                </Tooltip>
                                            </ToggleButton>
                                        </ToggleButtonGroup>
                                    </Box>
                                </Box>) 
                        }
                        
                        <Box sx={{display: "flex", flexDirection: "row", flexWrap: "wrap", width: "90%"}}>
                            <TextField value={background} onChange={(event) => { customized.push("background"); setCustomized(customized); setBackground(event.target.value || "") }} label="Background" placeholder="lightblue url(...) fixed center no-repeat" sx={{marginTop: "20px"}}></TextField>
                            <TextField value={boxshadow} onChange={(event) => { customized.push("boxshadow"); setCustomized(customized); setBoxshadow(event.target.value || "") }} label="Box Shadow" placeholder="20px 20px 20px 10px grey inset" sx={{marginTop: "20px"}}></TextField>
                            {
                                layout == "flex"? (<Box sx={{marginLeft: "5px", display: "flex", flexDirection: "column"}}>
                                        <Typography sx={{color: "var(--cds-text-secondary)"}}>Direction</Typography>
                                        <ToggleButtonGroup exclusive value={direction} onChange={(evt, directionValue) => { if(!!directionValue){ customized.push("background"); setCustomized(customized); setDirection(directionValue) }}} aria-label="layout=selection">
                                            <ToggleButton value="row" aria-label="row">
                                                <Tooltip title="Row">
                                                    <EastTwoTone />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value="column" aria-label="column">
                                                <Tooltip title="Column">
                                                    <SouthTwoTone />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value="row-reverse" aria-label="row-reverse">
                                                <Tooltip title="Row-reverse">
                                                    <WestTwoTone />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value="column-reverse" aria-label="column-reverse">
                                                <Tooltip title="Column-reverse">
                                                    <NorthTwoTone />
                                                </Tooltip>
                                            </ToggleButton>
                                        </ToggleButtonGroup>
                                    </Box>): null 
                            }
                            <Box sx={{marginLeft: "5px", display: "flex", flexDirection: "column"}}>
                                <Typography sx={{color: "var(--cds-text-secondary)"}}>Align Content</Typography>
                                <ToggleButtonGroup exclusive value={alignContent} onChange={(evt, alignContentValue) => { if(!!alignContentValue) { customized.push("alignContent"); setCustomized(customized); setAlignContent(alignContentValue); } }} aria-label="aligncontent=selection">
                                    <ToggleButton value="center" aria-label="center">
                                        <Tooltip title="Center">
                                            <AlignHorizontalCenter />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value={"flex-start"} aria-label={"flex-start"} sx={{ display: layout == "flex"? "block": "none"}}>
                                        <Tooltip title="Start">
                                            <FileDownloadTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value={"flex-end"} aria-label={"flex-end"} sx={{ display: layout == "flex"? "block": "none"}}>
                                        <Tooltip title="End">
                                            <FileUploadTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="space-around" aria-label="space-around">
                                        <Tooltip title="Space Around">
                                            <CreditCardTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="space-between" aria-label="space-between">
                                        <Tooltip title="Space Between">
                                            <ViewDayTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="stretch" aria-label="stretch">
                                        <Tooltip title="Stretch">
                                            <Height />
                                        </Tooltip>
                                    </ToggleButton>
                                </ToggleButtonGroup>
                            </Box>
                            <Box sx={{marginLeft: "5px", display: "flex", flexDirection: "column"}}>
                                <Typography sx={{color: "var(--cds-text-secondary)"}}>Justify Content</Typography>
                                <ToggleButtonGroup exclusive value={justifyContent} onChange={(evt, justifyContentValue) => { if(!!justifyContentValue) { customized.push("justifyContent"); setCustomized(customized); setJustifyContent(justifyContentValue); } }} aria-label="justifycontent=selection">
                                    <ToggleButton value="center" aria-label="center">
                                        <Tooltip title="Center">
                                            <AlignVerticalCenter />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value={layout == "grid"? "start": "flex-start"} aria-label={layout == "grid"? "start": "flex-start"}>
                                        <Tooltip title="Start">
                                            <FormatAlignLeft />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value={layout == "grid"? "end": "flex-end"} aria-label={layout == "grid"? "end": "flex-end"}>
                                        <Tooltip title="End">
                                            <FormatAlignRight />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="space-around" aria-label="space-around">
                                        <Tooltip title="Space Around">
                                            <WidthFullTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="space-between" aria-label="space-between">
                                        <Tooltip title="Space Between">
                                            <ViewArrayTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="space-evenly" aria-label="space-evenly">
                                        <Tooltip title="Space Evenly">
                                            <PauseTwoTone />
                                        </Tooltip>
                                    </ToggleButton>
                                </ToggleButtonGroup>
                            </Box>
                            <Box sx={{marginLeft: "5px", display: "flex", flexDirection: "column"}}>
                                <Typography sx={{color: "var(--cds-text-secondary)"}}>Align Items</Typography>
                                <ToggleButtonGroup exclusive value={alignItems} onChange={(evt, alignItemsValue) => { if(!!alignItemsValue) { customized.push("alignItems"); setCustomized(customized); setAlignItems(alignItemsValue) }}} aria-label="alignitems=selection">
                                    <ToggleButton value="center" aria-label="center">
                                        <Tooltip title="Center">
                                            <AlignVerticalCenter />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value={layout == "grid"? "start": "flex-start"} aria-label={layout == "grid"? "start": "flex-start"}>
                                        <Tooltip title="Start">
                                            <AlignVerticalTop />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value={layout == "grid"? "end": "flex-end"} aria-label={layout == "grid"? "end": "flex-end"}>
                                        <Tooltip title="End">
                                            <AlignVerticalBottom />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="stretch" aria-label="stretch">
                                        <Tooltip title="Stretch">
                                            <Height />
                                        </Tooltip>
                                    </ToggleButton>
                                    <ToggleButton value="baseline" aria-label="baseline">
                                        <Tooltip title="Baseline">
                                            <Foundation />
                                        </Tooltip>
                                    </ToggleButton>
                                </ToggleButtonGroup>
                            </Box>
                            {
                                layout == "grid"? (
                                    <Box sx={{marginLeft: "5px", display: "flex", flexDirection: "column"}}>
                                        <Typography sx={{color: "var(--cds-text-secondary)"}}>Justify Items</Typography>
                                        <ToggleButtonGroup exclusive value={justifyItems} onChange={(evt, justifyItemsValue) => { if(!!justifyItemsValue) { customized.push("justifyItems"); setCustomized(customized); setJustifyItems(justifyItemsValue) }}} aria-label="justifyitems=selection">
                                            <ToggleButton value="center" aria-label="center">
                                                <Tooltip title="Center">
                                                    <AlignVerticalCenter />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value={"start"} aria-label={"start"}>
                                                <Tooltip title="Start">
                                                    <FormatAlignLeft />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value={"end"} aria-label={"end"}>
                                                <Tooltip title="End">
                                                    <FormatAlignRight />
                                                </Tooltip>
                                            </ToggleButton>
                                            <ToggleButton value="stretch" aria-label="stretch">
                                                <Tooltip title="Stretch">
                                                    <Height />
                                                </Tooltip>
                                            </ToggleButton>
                                        </ToggleButtonGroup>
                                    </Box>
                                ): null
                            }
                        </Box>
                    </Box>
                ): null
            }
            {
                activeStep == 2? (
                    <Box sx={{ marginLeft: "5px", flexWrap: "wrap", display: "flex", flexDirection: "row", width: "100%" }}>
                        <TextField value={height} onChange={(event) => { customized.push("height"); setHeight(event.target.value || "") }} label="Height" placeholder="100px" sx={{marginTop: "20px"}}></TextField>
                        <TextField value={width} onChange={(event) => { customized.push("width"); setWidth(event.target.value || "") }} label="Width" placeholder="100px" sx={{marginTop: "20px"}}></TextField>
                        <TextField value={minHeight} onChange={(event) => { customized.push("minHeight"); setMinHeight(event.target.value || "") }} label="Min Height" placeholder="100px" sx={{marginTop: "20px"}}></TextField>
                        <TextField value={minWidth} onChange={(event) => { customized.push("minWidth"); setMinWidth(event.target.value || "") }} label="Min Width" placeholder="100px" sx={{marginTop: "20px"}}></TextField>
                        <TextField value={maxHeight} onChange={(event) => { customized.push("maxHeight"); setMaxHeight(event.target.value || "") }} label="Max Height" placeholder="100px" sx={{marginTop: "20px"}}></TextField>
                        <TextField value={maxWidth} onChange={(event) => { customized.push("maxWidth"); setMaxWidth(event.target.value || "") }} label="Max Width" placeholder="100px" sx={{marginTop: "20px"}}></TextField>    
                    </Box>
                ): null
            }
        </Box>
    );
};



const styles = `
  /* Custom styles for the Accordion component */
`;

const props = [
    {
        name: "defaultstyles",
        defaultValue: {},
        type: "object"
    }
];

const slots = [];

const WrappedComponent = wrapper(
    React.forwardRef(StyleContainer),
    props,
    "mui"
  );
export { WrappedComponent, styles, props, dispatchedEvents, slots };
